___CSS_LOADER_EXPORT___.push([module.id, `body {
    font-family: 'Mitr', sans-serif;
    margin: 0;
    background-color: #543f4c;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100vh;
}

/* Janela Estatisticas */
.janela {
    display: none;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.7);
    z-index: 9999;
  }
  
  .janela-content {
    background-color: #4f404b;
    color: #fff;
    font-weight: 300;
    width: 80%;
    max-width: 600px;
    margin: 50px auto;
    padding: 20px;
    border-radius: 10px;
    position: relative;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
  }
  
  .btn-fechar {
    position: absolute;
    top: 10px;
    right: 10px;
    font-size: 40px;
    cursor: pointer;
  }
  
  .grafico {
    display: flex;
    flex-direction: column;
    gap: 10px;
  }
  
  .barra {
    height: 40px;
    border-radius: 5px;
    color: #fff;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    position: relative;
    padding-left: 30px;
}

.barra p {
    height: 50%;
    color: black;
    font-weight: 500;
    padding-bottom: 1.5%;
    text-align: center;
    font-size: 18px;
    border-radius: 5px;
    background-color: #00cc99;
    width: 0%;
}

.barra span {
    font-size: 20px;
    text-align: center;
    position: absolute;
    left: 5px;
}
  
  .btn-estatisticas {
    position: fixed;
    top: 20px;
    right: 20px;
    background-color: #382732;
    color: #fff;
    padding: 10px 20px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.2s ease
  }
  
  .btn-estatisticas:hover {
    background-color: #2a2026;
  }

/* Jogo */
.game-container {
    text-align: center;
    background-color: #60475c;
    border-radius: 10px;
    padding: 30px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
}

.word-display {
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 28px;
    margin-bottom: 20px;
}

.letter {
    display: inline-block;
    cursor: pointer;
    width: 63px;
    height: 63px;
    line-height: 63px;
    text-align: center;
    color: #e8e8e8;
    border: 4px solid #382732;
    background-color: #3f2f39;
    margin: 0 5px;
    border-radius: 5px;
    font-size: 43px;
    font-weight: 600;
}

.letter:hover {
    outline: solid 1px #382732;
}

/* Teclado */
.teclado {
    display: flex;
    flex-direction: column;
    gap: 5px;
    margin-top: 20px;
}

.row {
    display: flex;
    justify-content: center;
    gap: 5px;
}

.key {
    padding: 12px 25px;
    background-color: #382732;
    color: #fff;
    border: none;
    border-radius: 10px;
    cursor: pointer;
    font-size: 22px;
    font-weight: 600;
    transition: background-color 0.2s ease;
}

.key:hover {
    background-color: #2a2026;
}

/* Mensagem Final */
.mensagem-container {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 0;
    overflow: hidden;
    transition: height 0.3s ease-in-out;
}

.mensagem-final {
    background-color: #5aad5a;
    color: white;
    cursor: pointer;
    padding: 10px 20px;
    border-radius: 5px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.3);
    opacity: 0;
    transform: translateY(-100%);
    transition: opacity 0.3s ease, transform 0.3s ease;
}

.show-element {
    height: auto;
    overflow: visible;
}

.show-element .mensagem-final {
    opacity: 1;
    transform: translateY(0);
}

.material-symbols-outlined {
  font-variation-settings:
  'FILL' 0,
  'wght' 300,
  'GRAD' 0,
  'opsz' 48
}

/* Ajustes para Smartphone */
@media (max-width: 768px) {
    .game-container {
        width: 90%;
        padding: 1em;
    }

    .row {
        gap: 0.2em;
    }

    .letter {
        width: 9em;
        height: 1.3em;
        line-height: 1.3em;
        border: 0.1em solid #382732;
        margin: 0px 0.1em;
        border-radius: 5px;
        font-size: 43px;
        font-weight: 600;
    }

    .key {
        width: 3em;
        height: 3em;
        line-height: 1em;
        padding: 0.5em;
        font-size: 1.2em;
    }
}`, "",{"version":3,"sources":["webpack://./src/views/styles.css"],"names":[],"mappings":"AAAA;IACI,+BAA+B;IAC/B,SAAS;IACT,yBAAyB;IACzB,aAAa;IACb,sBAAsB;IACtB,mBAAmB;IACnB,uBAAuB;IACvB,aAAa;AACjB;;AAEA,wBAAwB;AACxB;IACI,aAAa;IACb,eAAe;IACf,MAAM;IACN,OAAO;IACP,WAAW;IACX,YAAY;IACZ,oCAAoC;IACpC,aAAa;EACf;;EAEA;IACE,yBAAyB;IACzB,WAAW;IACX,gBAAgB;IAChB,UAAU;IACV,gBAAgB;IAChB,iBAAiB;IACjB,aAAa;IACb,mBAAmB;IACnB,kBAAkB;IAClB,uCAAuC;EACzC;;EAEA;IACE,kBAAkB;IAClB,SAAS;IACT,WAAW;IACX,eAAe;IACf,eAAe;EACjB;;EAEA;IACE,aAAa;IACb,sBAAsB;IACtB,SAAS;EACX;;EAEA;IACE,YAAY;IACZ,kBAAkB;IAClB,WAAW;IACX,aAAa;IACb,mBAAmB;IACnB,2BAA2B;IAC3B,kBAAkB;IAClB,kBAAkB;AACtB;;AAEA;IACI,WAAW;IACX,YAAY;IACZ,gBAAgB;IAChB,oBAAoB;IACpB,kBAAkB;IAClB,eAAe;IACf,kBAAkB;IAClB,yBAAyB;IACzB,SAAS;AACb;;AAEA;IACI,eAAe;IACf,kBAAkB;IAClB,kBAAkB;IAClB,SAAS;AACb;;EAEE;IACE,eAAe;IACf,SAAS;IACT,WAAW;IACX,yBAAyB;IACzB,WAAW;IACX,kBAAkB;IAClB,YAAY;IACZ,kBAAkB;IAClB,eAAe;IACf;EACF;;EAEA;IACE,yBAAyB;EAC3B;;AAEF,SAAS;AACT;IACI,kBAAkB;IAClB,yBAAyB;IACzB,mBAAmB;IACnB,aAAa;IACb,uCAAuC;AAC3C;;AAEA;IACI,aAAa;IACb,uBAAuB;IACvB,mBAAmB;IACnB,eAAe;IACf,mBAAmB;AACvB;;AAEA;IACI,qBAAqB;IACrB,eAAe;IACf,WAAW;IACX,YAAY;IACZ,iBAAiB;IACjB,kBAAkB;IAClB,cAAc;IACd,yBAAyB;IACzB,yBAAyB;IACzB,aAAa;IACb,kBAAkB;IAClB,eAAe;IACf,gBAAgB;AACpB;;AAEA;IACI,0BAA0B;AAC9B;;AAEA,YAAY;AACZ;IACI,aAAa;IACb,sBAAsB;IACtB,QAAQ;IACR,gBAAgB;AACpB;;AAEA;IACI,aAAa;IACb,uBAAuB;IACvB,QAAQ;AACZ;;AAEA;IACI,kBAAkB;IAClB,yBAAyB;IACzB,WAAW;IACX,YAAY;IACZ,mBAAmB;IACnB,eAAe;IACf,eAAe;IACf,gBAAgB;IAChB,sCAAsC;AAC1C;;AAEA;IACI,yBAAyB;AAC7B;;AAEA,mBAAmB;AACnB;IACI,eAAe;IACf,MAAM;IACN,OAAO;IACP,WAAW;IACX,aAAa;IACb,sBAAsB;IACtB,uBAAuB;IACvB,mBAAmB;IACnB,SAAS;IACT,gBAAgB;IAChB,mCAAmC;AACvC;;AAEA;IACI,yBAAyB;IACzB,YAAY;IACZ,eAAe;IACf,kBAAkB;IAClB,kBAAkB;IAClB,wCAAwC;IACxC,UAAU;IACV,4BAA4B;IAC5B,kDAAkD;AACtD;;AAEA;IACI,YAAY;IACZ,iBAAiB;AACrB;;AAEA;IACI,UAAU;IACV,wBAAwB;AAC5B;;AAEA;EACE;;;;;AAKF;;AAEA,4BAA4B;AAC5B;IACI;QACI,UAAU;QACV,YAAY;IAChB;;IAEA;QACI,UAAU;IACd;;IAEA;QACI,UAAU;QACV,